<template>
  <div>
    <h1>DETAILS</h1>
    <img :src="product.product_imagelink" />
    <br />
    {{ product }}
  </div>
</template>

<script>
export default {
  data() {
    return {
      product: {}
    };
  },
  created() {
    var id = this.$route.params._id;
    this.$http.get(`/data/${id}`).then(response => {
      this.product = response.data;
    });
  }
};
</script>
